.gs-section{
   background-color:$color3;
   color: $color2;
   padding-block: 76px;

   .main-layout{
      gap: 260px;
      justify-content: center;
   }

   .contact-btn{
      padding: 10px 32px;
      border: 2px solid $color2;
      border-radius: 10rem;
      align-self: center;
      font-size: 1.125rem;
      font-weight: 500;
      color: $color2;
      
      &:hover{
         color: $white;
         background-color:$color2;
      }
   }

   .decoration{
      position: absolute;
      left: 0;
      bottom: 0;
   }
   
   @include for-medium-layout{
      .main-layout{
         gap: 180px;
      }
      
      .decoration{
         bottom: -20px;
      }
   }
   @include for-tablet-layout{
      .main-layout{
         gap: 50px;
      }
   }
   @include for-small-layout{
      .main-layout{
         flex-direction: column;
         align-items: center;
         text-align: center;
      }
   }
}