.app-footer{
   background-color: $color4;
   padding-block: 48px;

   .main-layout{
      justify-content: space-between;
   }

   .left-container{
      gap: 125px;

      .brand-nav{
         flex-direction: column;
         justify-content: space-between;
      }

      p{
         font-size: 0.9375rem;
         line-height: 25px;
      }
   }

   nav{
      gap:40px;

      a{
         font-size: 1.125rem;
      }
   }
   
   .right-container{
      justify-content: space-between;

      .media-icons{
         gap: 16px;
         align-self: end;

         .icon:hover{
            fill: $color3;
         }
      }

      small{
         font-size: 0.9375rem;
      }
   }

   @include for-tablet-layout{
      padding-block: 56px;

      .main-layout{
         flex-direction: column;
         gap: 32px;

         &>*{
            justify-content: space-between;
         }

         .right-container{
            flex-direction: row;
         }

         .left-container{
            gap: unset;
            p{
               text-align: end;
            }
         }
      }
   }
   
   @include for-mobile-layout{
      padding-block: 65px;

      .main-layout{
         gap: 40px;

         .right-container,.left-container{
            flex-direction: column;
            align-items: center;
         }

         .left-container{
            gap: 24px;

            p{
               text-align: center;
            }
         }

         .right-container{
            gap: 16px;
         }

         .brand-nav{
            gap: 24px;

            nav{
               justify-content: space-between;
            }
         }

         .media-icons{
            align-self: unset;
         }
      }

   }
}