.main-layout{
   max-width: 1110px;
   margin: auto;
   
   @include for-medium-layout{
      max-width: 850px;
   }

   @include for-tablet-layout{
      max-width: 650px;
   }

   @include for-small-layout{
      max-width: 500px;
   }

   @include for-mobile-layout{
      max-width: unset;
      margin: 0 24px;
   }
}