* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    font-size: 16px;
    font-family: $main-font;
    color: $white;
}

body {
    background-color: $color1;
    margin: 0;
}

button {
    cursor: pointer;
    border: none;
    background-color: rgba(255, 255, 255, 0);
}

button,
input {
    font-family: $main-font;
}


a {
    color: inherit;
    text-decoration: none;
}