.home-page{
   section{
      overflow: hidden;
      position: relative;
   }

   .section-title{
      font-size: 3rem;
   }

   @include for-medium-layout{ 
      .section-title{
         font-size: 2.5rem;
         line-height: 2.5rem;
      }
   }

   @include for-tablet-layout{ 
      .section-title{
         font-size: 2rem;
         line-height: 2rem;
      }
   }
}