.app-header{
   justify-content: space-between;
   align-items: center;

   .brand-nav{
      align-items: center;
      gap:80px;
   }

   nav{
      gap:40px;
   }
   
   .contact-btn{
      font-size: 1.125rem;
      color: $white;
      padding: 10px 33px;
      border: solid 2px $white;
      border-radius: 10rem;
      align-self: center;

      &:hover{
         background-color: $white;
         color: $color4;
      }
   }

   @include for-small-layout{
      nav,.contact-btn{
         display: none;
      }

      .hamburger{
         display: inline-block;
      }
   }
}