.pros-section{
   background-color: $color2;
   padding-block: 140px;

   .main-layout{
      gap: 125px;
   }

   .section-title{
      gap: 54px;

      &::before{
         content: '';
         width: 50px;
         height:4px;
         background-color: $color3;
      }
   }

   .cards-container{
      margin-top: 58px;
      gap:32px;
      min-width: 540px;
   }

   .pro-card{
      gap: 23px;

      img{
         align-self: start;
      }

      .text-container{
         line-height: 25px;
         gap:16px;
      }

      h4{
         color:$color3;
         font-size: 1.125rem;
      }
   }
   

   .decoration-right{
      position: absolute;
      right: 0;
      bottom: 0;
      transform: translateX(50%);
   }

   @include for-medium-layout{
      .main-layout{
         gap: 50px;
      }
      
      .cards-container{
         min-width: unset;
      }
   }
   
   @include for-tablet-layout{
      padding-block:64px;

      .main-layout{
         flex-direction: column;
         gap: 64px;
      }

      .section-title{
         max-width: 70%;
      }

      .cards-container{
         margin-top: 0;
      }

      .decoration-right{
         top: 0;
      }
   }

   @include for-mobile-layout{
      .cards-container{
         gap:48px;

         .pro-card{
            flex-direction: column;
            align-items: center;
            text-align: center;
            gap: 16px;

            img{
               align-self: unset;
            }
         }
      }
   }
}