.hero-section{
   padding-block:73px 250px;

   .main-layout{
      gap:129px;
   }

   .hero-container{
      gap: 30px;

      .slogan{
         font-size: 6.25rem;
         line-height: 6.25rem;
         flex: 1;
         
         span{
            color: $color3;
         }
      }
      
      p{
         width: 445px;
         font-size: 1.125rem;
         font-weight: 500;
         line-height: 28px;
         justify-content: space-between;

         &::before{
            content:'';
            width: 50px;
            height: 4px;
            background-color: $color5;
         }
      }
   }

   .left-decoration{
      position: absolute;
      left: 0;
      top: 50%;
      transform: translate(-50%, -50%);
      z-index: -1;
   }

   .bottom-decoration{
      position: absolute;
      right: 20%;
      bottom:0;
      transform: translate(50%, 50%);
      z-index: -1;
   }


   @include for-medium-layout{
      .hero-container{
         .slogan{
            line-height: 5rem;
            font-size: 5rem;
         }
         
         p{
            width: 331px;
         }
      }
   }
   
   @include for-tablet-layout{
      .main-layout{
         gap:112px;
      }

      .hero-container{
         flex-direction: column;
         align-items: center;
         text-align: center;
         gap: 24px;
         
         .slogan{
            line-height: 4rem;
            font-size: 3.5rem;
         }
         
         p{
            font-size: 0.9375rem;
            width: 457px ;
            
            &::before{
               display: none;
            }
         }
      }
      
      .bottom-decoration{
         right: 50%;
      }
      
      .left-decoration{
         display: none;
      }
   }

   @include for-mobile-layout{
      padding-block:48px 197px;
      .hero-container{

         .slogan{
            line-height: 2.5rem;
            font-size: 2.5rem;
         }

         p{
            width: unset;
         }
      }
   }
}